* {
    box-sizing: border-box;
    font-family: "Quicksand", sans-serif;
}

html,
body {
    margin: 0;
}

.full-screen-container {
    background-image: url("background_image.jpg");
    height: 100vh;
    width: 100vw;
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-container {
    background-color: hsla(201, 100%, 6%, 0.6);
    padding: 50px 30px;
    width: 50%;
    min-width: 400px;
    max-width: 600px;
}

.login-title {
    color: white;
    text-align: center;
    margin: 0 0 40px;
    font-size: 2.5em;
    font-weight: normal;
}

.input-group {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.button-group span,
.input-group label {
    color: white;
    font-weight: lighter;
}

.button-group {
    text-align: center;
}

.button-group span {
    font-size: 1.2em;
}

.button-group span button {
    color: goldenrod !important;
}

.button-group span button:hover {
    background-color: hsla(180, 100%, 20%, 0.3);
}

.input-group label {
    font-size: 1.5em;
    margin-bottom: 7px;
}

.input-group input {
    font-size: 1.5em;
    padding: 0.1em 0.25em;
    background-color: hsla(201, 100%, 91%, 0.3);
    border: 1px solid hsl(201, 100%, 6%);
    outline: none;
    border-radius: 5px;
    color: white;
    font-weight: lighter;
}

.input-group input:focus {
    border: 1px solid hsl(201, 100%, 50%);
}

.login-button {
    padding: 10px 30px;
    width: 100%;
    border-radius: 5px;
    background-color: hsla(201, 100%, 50%, 0.1);
    border: 1px solid hsl(201, 100%, 50%);
    outline: none;
    font-size: 1.5em;
    color: white;
    font-weight: lighter;
    margin-top: 20px;
    cursor: pointer;
}

.login-button:hover {
    background-color: hsla(180, 100%, 20%, 0.3);
}

.login-button:focus {
    background-color: hsla(201, 100%, 50%, 0.5);
}

.link-button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    text-decoration: underline;
    display: inline;
    margin: 0;
    padding: 0;
}